.header {
  position: sticky;
  display: flex;
  height: 5rem;
  align-items: center;
}
.Airbnb {
  flex: 5 1 auto;
}
.Airbnb > .logo {
  margin-left: 86px;
  max-width: 8rem;
}
.Airbnb >.logo>a{
  color: var(--baseColor-air);
}
.Airbnb > .logo > svg {
  color: var(--baseColor-air);
}
.menber {
  display: flex;
  flex: 1 1 auto;
  align-items: center;
}
.twice > svg {
  height: 20px;
  width: 20px;
}
.twice {
  margin: 0 25px;
}
.ones > span {
  font-size: 20px;
  font-weight: 600;
}
.last {
  border: var(--line-push);
  position: relative;
  display: flex;
  border-radius: 60px;
  align-items: center;
}

.menber > .last {
  width: 80px;
  height: 41px;
  margin: 9px;
  cursor: pointer;
}
.menu > svg {
  display: block;
  fill: none;
  height: 30px;
  width: 20px;
  stroke: currentcolor;
  stroke-width: 3;
  overflow: visible;
}
.last > .small {
  border-radius: 50%;
  background: var(--baseColor-air);
  width: 10px;
  height: 10px;
  border: 3px solid var(--font-color-white);
}
.last > .small {
  position: absolute;
  right: 3px;
  top: 0;
}
.last > .menu {
  margin-left: 12px;
}
.last > .sign {
  margin-left: 7px;
}
.last > .sign > svg {
  width: 35px;
  height: 35px;
}
.menber > .ones {
  padding: 12px;
  border-radius: 34px;
  cursor: pointer;
  transition: all .5s linear;
}
.menber > .ones:hover {
  background: var(--color-tranparent);
}
.ones > span > a{
  color: currentColor;
  text-decoration: none;
}
@media only screen and (max-width: 670px) and (min-width: 451px) {
  .header {
    background: greenyellow;
  }
  .ones {
    display: none;
  }
  .logo {
    margin-left: 40px !important;
  }
}

@media only screen and (max-width: 450px) {
  .ones {
    display: none;
  }
  .Airbnb {
    width: 50%;
  }
  .Airbnb > .logo {
    margin-left: 40px;
  }
  .twice {
    margin: 0;
  }
  .menber {
    width: 50%;
    justify-content: flex-end;
  }
  .menber > .last {
    margin: 9px 15px;
  }
}
